import React, { Component } from 'react';
import featherIcons from '../../vendors/feather/feather-sprite.svg';
import Sidebar from './Sidebar';
import SearchBar from './SearchBar';

function SidebarIcon(props) {
  return (
    <svg
      className={`sidebar__icon feather feather-${props.name}`}
      xmlns="http://www.w3.org/2000/svg"
      xmlnsXlink="http://www.w3.org/1999/xlink"
      viewBox="0 0 24 24"
      width="24"
      height="24"
      fill="none"
      stroke="currentColor"
      strokeWidth="2"
      strokeLinecap="round"
      strokeLinejoin="round"
    >
      <use xlinkHref={`${featherIcons}#${props.name}`} />
    </svg>
  );
}

class Header extends Component {
  constructor(props) {
    super(props);
    this.state = {
      navIsExpanded: false
    };
    this.handleToggleClick = this.handleToggleClick.bind(this);
    this.handleClick = this.handleClick.bind(this);
  }

  handleToggleClick(e) {
    this.setState(state => ({
      navIsExpanded: !state.navIsExpanded
    }));
  }

  handleClick(e) {
    this.setState(state => ({
      navIsExpanded: false
    }));
    console.log(this.setState);
  }

  render() {
    return (
      <header
        className={`header${this.state.navIsExpanded && !this.props.hideNav ? ' expand-nav' : ''}${
          this.props.hideNav ? ' hide-nav' : ''
        }`}
      >
        <div className="sidebar__canvas">
          <button
            onClick={this.handleToggleClick}
            className={`sidebar__btn--toggle${this.props.hideToggle ? ' is-hidden' : ''}`}
          >
            <SidebarIcon name="arrow-right" />
          </button>
          <Sidebar />
        </div>
        <div className="top-bar clearfix">
          <SearchBar />
        </div>
      </header>
    );
  }
}

export default Header;



