import React, { Component } from 'react';
import logo from '../../logo.svg';
import jamIcons from '../../vendors/jam/jam-sprite.svg';
import Logo from './Logo';
import { NavLink } from 'react-router-dom';

function NavIcon(props) {
  return (
    <svg
      className={`nav__icon jam jam-${props.name}`}
      xmlns="http://www.w3.org/2000/svg"
      xmlnsXlink="http://www.w3.org/1999/xlink"
      viewBox="0 0 24 24"
      width="24"
      height="24"
      fill="currentColor"
      stroke="none"
      strokeWidth="2"
      strokeLinecap="round"
      strokeLinejoin="round"
    >
      <use xlinkHref={`${jamIcons}#${props.name}`} />
    </svg>
  );
}

class Sidebar extends Component {
  constructor(props) {
    super(props);
  }
  render() {
    return (
      <div className="sidebar">
        <nav className="sidebar__nav">
          <div className="sidebar__logo">
            <Logo size="sm" />
          </div>
          <ul className="sidebar__list">
            <li className="sidebar__item">
              <NavLink className="sidebar__link" to="/photography">
                <NavIcon name="camera" />
                <span className="sidebar__section-name">Photography</span>
              </NavLink>
            </li>
            <li className="sidebar__item">
              <NavLink className="sidebar__link" to="/graphics">
                <NavIcon name="brush" />
                <span className="sidebar__section-name">Graphics</span>
              </NavLink>
            </li>
            <li className="sidebar__item">
              <NavLink className="sidebar__link" to="/textures">
                <NavIcon name="ghost-org-square" />
                <span className="sidebar__section-name">Textures</span>
              </NavLink>
            </li>
            <li className="sidebar__item">
              <NavLink className="sidebar__link" to="/typography">
                <NavIcon name="background-color" />
                <span className="sidebar__section-name">Typography</span>
              </NavLink>
            </li>
            <li className="sidebar__item">
              <NavLink className="sidebar__link" to="/colors">
                <NavIcon name="eyedropper" />
                <span className="sidebar__section-name">Colors</span>
              </NavLink>
            </li>
            <li className="sidebar__item">
              <NavLink className="sidebar__link" to="/user-interface">
                <NavIcon name="layout" />
                <span className="sidebar__section-name">User Interface</span>
              </NavLink>
            </li>
          </ul>
        </nav>
      </div>
    );
  }
}

export default Sidebar;



