import React, { Component } from 'react';
import Card from './Card';
import CardGrid from './CardGrid';

class UserInterface extends Component {
  render() {
    return (
      <section className="section section--user-interface">
        <header className="section__header">
          <h2 className="section__title">User Interface</h2>
        </header>
        <div className="section__body">
          <CardGrid>
            <Card
              cardImg="https://images.g2crowd.com/uploads/product/image/large_detail/large_detail_1519939946/figma.jpg"
              cardURL="https://www.figma.com/"
              cardName="Figma"
              cardDesc="Design, prototype, and gather feedback all in one place with Figma. Free for individuals."
              imgHasBackground={true}
              cardCategory="Create prototypes"
            />
            <Card
              cardImg="https://pbs.twimg.com/profile_images/936579385049706498/vXDa4JBU_400x400.jpg"
              cardURL="https://uijar.com/"
              cardName="UI Jar"
              cardDesc="Handpicked website and design inspiration for your real life projects."
              imgHasBackground={true}
              cardCategory="Get UI inspiration"
            />
            <Card
              cardImg="https://res-4.cloudinary.com/toolmuse/image/upload/v1498781678/production_uxarchive-logo-600_ksinnf.png"
              cardURL="http://uxarchive.com/"
              cardName="UX Archive"
              cardDesc="An archive of user flows for your inspiration."
              imgHasBackground={true}
              cardCategory="Get UI inspiration"
            />
          </CardGrid>
        </div>
      </section>
    );
  }
}

export default UserInterface;



