import React, { Component } from 'react';
import featherIcons from '../../vendors/feather/feather-sprite.svg';

function AppIcon(props) {
  return (
    <svg
      className={`app__icon feather feather-${props.name}`}
      xmlns="http://www.w3.org/2000/svg"
      xmlnsXlink="http://www.w3.org/1999/xlink"
      viewBox="0 0 24 24"
      width="24"
      height="24"
      fill="none"
      stroke="currentColor"
      strokeWidth="2"
      strokeLinecap="round"
      strokeLinejoin="round"
    >
      <use xlinkHref={`${featherIcons}#${props.name}`} />
    </svg>
  );
}

export default AppIcon;



