import React, { Component } from 'react';
import NavGrid from './NavGrid';
import Logo from './Logo';

const Home = () => (
  <section className="section section--home">
    <h1 onClick={this.handleClick} className="header__title">
      <Logo size="lg" />
    </h1>
    <p className="header__sub-title">
      A collection of free digital design resources for creatives.
    </p>
    <NavGrid />
  </section>
);

export default Home;



