import React from 'react';
import featherIcons from '../../vendors/feather/feather-sprite.svg';
import { Link } from 'react-router-dom';

const Logo = props => {
  return (
    <div className={`app-logo app-logo--${props.size} ${props.display}`}>
      <Link className="app-logo__link" to="/">
        <svg
          className="feather feather-bookmark"
          xmlns="http://www.w3.org/2000/svg"
          xmlnsXlink="http://www.w3.org/1999/xlink"
          viewBox="0 0 24 24"
          width="24"
          height="24"
          fill="none"
          stroke="currentColor"
          strokeWidth="2"
          strokeLinecap="round"
          strokeLinejoin="round"
        >
          <use xlinkHref={`${featherIcons}#bookmark`} />
        </svg>
        <span className="app-name">Makerbook</span>
      </Link>
    </div>
  );
};

export default Logo;



