import React, { Component } from 'react';
import logo from '../../logo.svg';
import '../../css/styles.css';
import { Route, withRouter, Switch } from 'react-router-dom';
import Photography from './Photography';
import Graphics from './Graphics';
import Colors from './Colors';
import Textures from './Textures';
import Typography from './Typography';
import UserInterface from './UserInterface';
import Home from './Home';
import Header from './Header';

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isHomePage: true
    };
    this.handlePageChange = this.handlePageChange.bind(this);
  }

  componentWillMount() {
    this.unlisten = this.props.history.listen((location, action) => {
      if (location.pathname === '/') {
        this.handlePageChange(true);
      } else {
        this.handlePageChange(false);
      }
    });
  }
  componentWillUnmount() {
    this.unlisten();
  }

  handlePageChange(val) {
    this.setState({
      isHomePage: val
    });
  }

  render() {
    const { location } = this.props;
    console.log(this.state.isHomePage);
    console.log(location.pathname);

    return (
      <div className="app">
        <Header
          hideToggle={this.state.isHomePage}
          hideNav={this.state.isHomePage && location.pathname === '/'}
        />
        <Switch location={location}>
          <Route exact path="/" component={Home} />
          <Route path="/photography" component={Photography} />
          <Route path="/graphics" component={Graphics} />
          <Route path="/colors" component={Colors} />
          <Route path="/textures" component={Textures} />
          <Route path="/typography" component={Typography} />
          <Route path="/user-interface" component={UserInterface} />
        </Switch>
      </div>
    );
  }
}

export default withRouter(App);



