import React, { Component } from 'react';
import jamIcons from '../../vendors/jam/jam-sprite.svg';
import { Link } from 'react-router-dom';

function TileIcon(props) {
  return (
    <svg
      className={`tile__icon jam jam-${props.name}`}
      xmlns="http://www.w3.org/2000/svg"
      xmlnsXlink="http://www.w3.org/1999/xlink"
      viewBox="0 0 24 24"
      width="24"
      height="24"
      fill="currentColor"
      stroke="none"
      strokeWidth="2"
      strokeLinecap="round"
      strokeLinejoin="round"
    >
      <use xlinkHref={`${jamIcons}#${props.name}`} />
    </svg>
  );
}

class NavTile extends Component {
  constructor(props) {
    super(props);
  }

  render() {
    return (
      <div className="tile nav__tile">
        <Link className="tile__link" to={this.props.tileURL}>
          <TileIcon className="tile__icon" name={this.props.tileIcon} />
          <span className="tile__name">{this.props.tileName}</span>
        </Link>
      </div>
    );
  }
}

class NavGrid extends Component {
  constructor(props) {
    super(props);
  }

  render() {
    return (
      <div className="nav-grid">
        <NavTile tileName="Photography" tileURL="/photography" tileIcon="camera" />
        <NavTile tileName="Graphics" tileURL="/graphics" tileIcon="brush" />
        <NavTile tileName="Textures" tileURL="/textures" tileIcon="ghost-org-square" />
        <NavTile tileName="Typography" tileURL="/typography" tileIcon="background-color" />
        <NavTile tileName="Colors" tileURL="/colors" tileIcon="eyedropper" />
        <NavTile tileName="User Interface" tileURL="/user-interface" tileIcon="layout" />
      </div>
    );
  }
}

export default NavGrid;



